import React from 'react';
import Answer from './Answer/Answer';

import cls from './Question.module.css';



const Question = () => {

  //   state
  const [ showParse, setShowParse ] = React.useState(false);

  const handleShowParse = () => {
    setShowParse((prevState) => !prevState);
  }

  return (
    <div className={cls.question}>
      <div className={cls.question__header}>
        <div className={cls.question__number}>  #1 </div>
      </div>

      <div className={cls.question__text}>
                      ?
      </div>

      <div className = {cls.answers}>
        <Answer wrong/>
        <Answer />
        <Answer />
        <Answer correct/>
      </div>

      <React.Fragment>
        <div className = {cls.question__hint} onClick = { handleShowParse }> {showParse ? <span> C  </span>:<span>  </span>} </div>
        {showParse && <div className = {cls.hint__text}>   , , ,    .   , , ,    .
            , , ,    .   , , ,    .
            , , ,    . 
        </div>}
      </React.Fragment>
    </div>
  )
}

export default Question;